.About {
    margin: 0;
    padding: 0;
    position: relative;
    box-sizing: border-box;
    padding-top: 70px;
    width: 100%;
    height: 600px;
    background-image: url("../../assests/images/laptopbackground.jpg");
    background-size: cover;
    background-position-x: right;
}

.About_text {
    position: absolute;
    bottom: 15%;
    left: 10px;
    color: #FFFFFF;
    width: 90%;
    z-index: 1;
    text-align: start;
}

.About_text h1 {
    font-size: 1.2rem;
}

.My_heading {
    letter-spacing: -0.01em;
    color: #242424;
    text-shadow: -8px -8px 16px rgba(0, 0, 0, 0.25);
}

.About_Icon {
    height: 6rem;
    width: 6rem;
    z-index: 1;
    position: absolute;
    transform: rotate(-90deg);
    bottom: 0;
    left: 50%;
    margin-left: -3rem;
}

.About_Icon img {
    width: 100%;
    height: 100%;
    z-index: 1;
}

@media screen and (min-width: 499px) {
    .About {
        padding-top: 0;
    }
    .About_text h1 {
        font-size: 1.5em;
    }
}

@media screen and (min-width: 600px) and (max-width: 800px) {
    .About_text {
        bottom: 25%;
        left: 20px;
        width: 75%;
    }
    .About_text h1 {
        font-size: 1.7em;
    }
}

@media (min-width: 801px) {
    .About_text {
        position: absolute;
        bottom: 25%;
        left: 20px;
        width: 75%;
    }
    .About_text h1 {
        font-size: 2em;
    }
}