.Carousel {
    width: 100%;
    height: 100%;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 1fr 1fr;
}

.Carousel::before,
.Carousel::after {
    position: absolute;
    font-size: 64px;
    color: #666666;
    font-family: 'Montserrat';
    font-style: normal;
    height: 40px;
}

.Carousel::before {
    content: '“';
    top: 10px;
    left: 10px;
}

.Carousel::after {
    content: '”';
    right: 10px;
    bottom: 0;
}

.Carousel div {
    padding: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-flow: column;
    margin: 0 auto;
    width: 100%;
}

.Carousel div p {
    margin: 0;
    margin-top: 12px!important;
    padding: 10px;
}

.Div1 {
    grid-row-start: 2;
    grid-row-end: 3;
}

.Div2 {
    grid-row-start: 1;
}

.Div3 {
    grid-row-start: 3;
    align-items: flex-start!important;
    text-align: center;
}

.Carousel div h2,
.Carousel div h3 {
    margin: 0;
}

.Carousel div h2 {
    padding: 10px 0;
    font-size: 1em;
}

.Carousel div h3 {
    padding: 0;
    font-size: 0.9em;
}

.Carousel_Image {
    width: 122px;
    height: 126px;
    border-radius: 32px;
}

@media screen and (min-width: 600px) {
    .Div1 {
        grid-column-start: 1;
        grid-row-start: 1;
        grid-row-end: 2;
        justify-content: center!important;
        align-items: flex-end!important;
    }
    .Div2 {
        grid-column-start: 2;
        grid-row-start: 1;
        justify-content: center!important;
        align-items: flex-start!important;
    }
    .Div3 {
        grid-column-start: 1;
        grid-column-end: 3;
        grid-row-start: 2;
        justify-content: flex-start!important;
        width: 100%!important;
    }
    .Carousel {
        grid-template-columns: 3.5fr 1.5fr;
        grid-template-rows: 1fr 1fr;
    }
    .Carousel div h2 {
        font-size: 1.5em;
    }
    .Carousel div h3 {
        font-size: 1.17em;
    }
    .Carousel div p {
        margin-top: 0;
        font-size: 1.4rem;
    }
    .Carousel::before {
        top: 2rem;
        left: 4rem;
    }
    .Carousel::after {
        right: 3rem;
        bottom: .8rem;
    }
}

@media screen and (min-width: 1024px) {
    .Div3 {
        width: 85%!important;
        text-align: start;
    }
    .Carousel div p {
        font-size: 1.6rem;
        margin-top: 0;
    }
}