    .Portofolio {
        display: grid;
        grid-template-columns: 1fr;
        width: 100%;
        min-height: 320px;
        color: #2C2C2C;
    }
    
    .Portofolio_Image,
    .Portofolio_Text {
        display: flex;
        align-items: flex-start;
        padding: 10px;
    }
    
    .Portofolio_Text {
        grid-row-start: 2;
        grid-row-end: 3;
        flex-flow: column;
        text-align: start;
    }
    
    .Portofolio_Text h1 {
        text-transform: capitalize;
        font-family: 'Montserrat';
        font-style: normal;
        text-shadow: -8px 8px 13px rgba(0, 0, 0, 0.35);
        font-weight: 600;
    }
    
    .Portofolio_Image {
        flex-flow: row;
        justify-content: center;
        width: 100%;
        height: 100%;
        grid-row-start: 1;
        grid-row-end: 2;
    }
    
    .container {
        position: relative;
        width: 80%;
    }
    
    .image {
        display: block;
        width: 100%;
        height: auto;
        -webkit-transform: rotate(360deg) scale(1);
        -ms-transform: rotate(360deg) scale(1);
        transform: rotate(360deg) scale(1);
        -webkit-transition: .3s ease;
        transition: .3s ease;
    }
    
    .overlay {
        position: absolute;
        bottom: 0;
        left: 0;
        right: 0;
        background-color: #5E58F8;
        overflow: hidden;
        width: 100%;
        height: 100%;
        z-index: -1;
        display: flex;
        align-items: center;
        justify-content: center;
    }
    
    .container:hover .image {
        -webkit-transform: rotate(360deg) scale(0);
        -ms-transform: rotate(360deg) scale(0);
        transform: rotate(.0turn) scale(0);
    }
    
    .text {
        color: white;
        font-size: 1rem;
    }
    
    @media (min-width: 481px) and (max-width: 767px) {
        .Portofolio {
            grid-template-columns: 1fr;
            width: 70%;
            margin: 0;
        }
        .container {
            width: 90%;
        }
        .Portofolio_Text {
            grid-row-start: 2;
            grid-row-end: 3;
        }
    }
    
    @media (min-width: 481px) and (max-width: 1030px) {
        .text {
            color: white;
            font-size: .5rem;
        }
    }
    
    @media screen and (min-width: 768px) {
        .Portofolio {
            grid-template-columns: repeat(2, 1fr);
            min-height: 360px;
            width: 80%;
            margin: 30px 0;
        }
        .container {
            width: 80%;
        }
        .Portofolio_Text {
            grid-row-start: 1;
            grid-row-end: 2;
        }
    }