.Footer {
    width: 100%;
    padding: 0;
    color: #FFFFFF;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-flow: column;
}

.FooterImage_Coontainer {
    width: 100%;
    position: relative;
}

.Footer img {
    display: block;
    width: 100%;
    position: relative;
}

.Image_Container {
    display: block;
    background: none;
    width: 100%;
}

.Footer_Social_Container {
    width: 100%;
    display: flex;
    flex-flow: column;
    min-width: 200px;
    padding: 10px;
    background-color: #5E58F8;
    margin: 0;
    padding: 10px;
    min-height: 270px;
    justify-content: space-between;
}

.Footer_Social_Container h3 {
    margin: 0;
    padding: 0;
}

.Footer_Icons {
    align-self: flex-start;
}

.Footer_Icons a svg {
    margin-right: 10px;
    cursor: pointer;
    -webkit-transition: -webkit-transform .8s ease-in-out;
    transition: transform .8s ease-in-out;
}

.Footer_Icons a svg:hover,
.Footer_Icons a svg:focus {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
}

.Footer_Social_Container p {
    align-self: flex-end;
}

.Image_Container svg {
    display: block;
    width: 100%;
}

.Image_Containe {
    padding: 100px;
}

@media (min-width: 499px) {
    .Footer_Social_Container {
        padding: 0px 20px 20px 40px;
    }
    .Image_Container {
        display: block;
        width: 100%;
    }
}

@media (min-width: 768px) {
    .Footer_Social_Container {
        padding: 0px 20px 20px 80px;
    }
}