.AboutMe {
    box-sizing: border-box;
    width: 100%;
    min-height: 450px;
    display: grid;
    grid-template-columns: 1fr;
    padding: 70px 0;
    color: #2C2C2C;
}

.AboutMe_Column_1,
.AboutMe_Column_2 {
    display: flex;
    flex-flow: column;
    box-sizing: border-box;
    padding: 10px;
    justify-self: center;
    align-self: center;
}

.AboutMe_Column_1 {
    align-items: center;
    justify-content: center;
}

.Bio {
    align-self: center;
    justify-self: center;
    text-align: center;
    padding: 25px 0;
}

.remove_space {
    margin: 0;
    padding: 0;
}

.AboutMe_Image {
    width: 232px;
    height: 232px;
}

.AboutMe_Image img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 50%;
    box-sizing: border-box;
    border: 5px solid blueviolet;
}

.Achievement {
    display: flex;
    width: 100%;
}

.Main_heading {
    font-size: 36px;
    text-shadow: -8px 8px 13px rgba(0, 0, 0, 0.25);
}

.Achievement_heading {
    font-size: 18px;
    text-shadow: -8px 8px 13px rgba(0, 0, 0, 0.25);
}

.Education,
.Certification {
    text-align: start;
    width: 50%;
}

.Education {
    position: relative;
    left: 0;
}

.Achievement_list {
    padding: 0px 0px 0px 20px;
    font-size: 14px;
    line-height: 17px;
    list-style: none;
}

.Achievement_list li:before {
    content: "\2022";
    background: -webkit-linear-gradient(119.66deg, #1400FF -10.2%, #8201FA 78.84%, #FF03F5 179.65%);
    background-clip: content-box;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    font-weight: bold;
    display: inline-block;
    width: 0.5rem;
    font-size: larger;
}

.Achievement_list li {
    margin: 8px 0;
}

@media (min-width: 600px) and (max-width: 767px) {
    .Education {
        left: 10%;
    }
}

@media (min-width: 768px) and (orientation: landscape) {
    .Achievement_heading {
        font-size: 36px;
    }
    .Achievement_list {
        font-size: 17px;
        line-height: auto;
    }
}

@media screen and (min-width: 920px) {
    .AboutMe {
        grid-template-columns: 1.5fr 3.5fr;
        width: 95%;
    }
    .Bio {
        align-self: start;
        justify-self: start;
        text-align: left;
    }
}