.Input,
.Text_Area {
    width: 100%;
    border: none;
    border-bottom: 3px solid white;
    background-color: transparent;
    padding: 0;
    margin: 0;
}

.Input {
    height: 40px;
}

.Text_Area {
    resize: none;
    height: 100%;
}

.Input:hover,
.Input:focus,
.Input:active,
.Text_Area:focus,
.Text_Area:active,
.Text_Area:hover {
    border: 3px solid white;
    outline-color: transparent;
}

.danger:hover,
.danger:focus {
    border: 3PX solid rgb(226, 95, 95)!important;
}

.success:focus,
.success:hover {
    border: 3px solid rgb(138, 204, 138)!important;
}

.success {
    border: 3px solid rgb(138, 204, 138)!important;
}

.danger {
    border: 3PX solid rgb(226, 95, 95)!important;
}

.feedback {
    font-size: x-small;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: flex-start;
    flex-flow: column;
}

.feedback_2 {
    font-size: x-small;
    width: 100%;
}

.feedback_2 p,
.feedback p {
    padding: 0;
    margin: 0;
    text-align: start;
    color: red;
    padding-bottom: 10px;
}