.NavigationItem {
    margin: 10px 0;
    display: block;
    box-sizing: border-box;
    width: 180px;
    text-align: left;
    display: flex;
    flex-direction: row;
}

.NavigationItem a {
    box-sizing: border-box;
    text-decoration: none;
    text-transform: uppercase;
    display: block;
    align-items: flex-end;
    display: flex;
    align-items: center;
    justify-items: center;
    color: #242424;
    text-transform: capitalize;
}

.NavigationItem a:hover,
.NavigationItem a:active,
.NavigationItem a.active {
    border-bottom: 4px solid #8743FF;
}

.NavigationItem img {
    height: 50px;
    width: 50px;
    display: block;
}

@media screen and (min-width: 768px) {
    .NavigationItem {
        margin: 0;
        width: auto;
        height: 100%;
        display: flex;
        align-items: center;
    }
    .NavigationItem a {
        height: 100%;
        padding: 16px 10px;
        border-bottom: 4px solid transparent;
    }
    .NavigationItem a:hover,
    .NavigationItem a:active,
    .NavigationItem a.active {
        border-bottom: 4px solid #8743FF;
    }
    .NavigationItem img {
        display: none;
    }
}