.Services {
    width: 100%;
    padding: 70px 15px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-flow: column;
    color: #2C2C2C;
}

.Services_heading {
    align-self: start;
    margin: 0;
    margin-bottom: 30px;
    text-shadow: -8px 8px 13px rgba(0, 0, 0, 0.35);
    text-transform: capitalize;
    width: 100%;
    text-align: start;
    font-size: 2.5em;
}

.Services_Container {
    width: 100%;
    min-height: 500px;
    display: grid;
    grid-template-columns: 1fr;
    gap: 8px;
}


/* Tablets, ipads(Portrait)*/

@media (min-width: 499px) {
    .Services_heading {
        padding-left: 10%;
        font-size: 4.5em;
    }
}

@media (min-width: 768px) and (max-width: 1024px) {
    .Services_Container {
        grid-template-columns: 1fr 1fr;
        width: 90%;
        gap: 15px;
    }
}

@media (min-width: 1025px) {
    .Services_Container {
        grid-template-columns: 1fr 1fr 1fr 1fr;
    }
    .Services {
        width: 95%;
    }
}