.Form {
    display: grid;
    grid-template-columns: 1fr;
    gap: 10px;
    margin-top: 15px;
    margin-bottom: 25px;
}

.First_Form_container,
.Second_Form_container {
    position: relative;
    height: 180px;
}

.First_Form_container {
    display: flex;
    flex-flow: column;
    justify-content: space-between!important;
}

.Second_Form_container {
    display: flex;
    align-self: flex-end;
    justify-content: flex-end;
}

@media screen and (min-width: 768px) {
    .Form {
        grid-template-columns: 1fr 1fr;
        margin: 50px 0;
    }
}