.Message {
    width: 100%;
    min-height: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: bold;
}

.Message.success {
    background-color: rgb(151, 218, 151);
    color: #FFFFFF;
}

.Message.error {
    background-color: rgb(218, 115, 115);
    color: #FFFFFF;
}