.Portofolios {
    width: 100%;
    min-height: 400px;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 70px 20px;
    flex-flow: column;
}

.Portofolios_heading {
    margin: 0;
    margin-bottom: 50px;
    align-self: start;
    text-shadow: -8px 8px 13px rgba(0, 0, 0, 0.35);
    font-size: 2.5em
}

@media (min-width: 499px) {
    .Portofolios_heading {
        padding-left: 10%;
        font-size: 4.5em;
    }
}

@media (min-width: 1025px) {
    .Portofolios {
        width: 95%;
    }
}