.Contact {
    width: 100%;
    min-height: 400px;
    display: flex;
    align-items: center;
    flex-flow: column;
    padding: 70px 15px;
    color: #2C2C2C;
}

.Contact_heading {
    align-self: start;
    margin: 0;
    margin-bottom: 16px;
    text-transform: capitalize;
    text-shadow: -8px 8px 13px rgba(0, 0, 0, 0.35);
    font-size: 2.5em;
}

.First_Contact__container {
    display: grid;
    width: 100%;
    min-height: 340px;
    grid-template-columns: 1fr;
    margin: 50px 0;
    gap: 50px
}

.Contact_info,
.Contact_map {
    display: flex;
    flex-flow: column;
}

.Contact_info {
    align-items: flex-start;
}

.Contact_map {
    align-items: center;
}

.Map_Container {
    width: 100%;
    height: 191px;
    border: 7px solid white;
    border-radius: 32px;
    overflow: hidden;
    box-shadow: 0 19px 38px rgba(0, 0, 0, 0.30), 0 15px 12px rgba(0, 0, 0, 0.22);
}

.ContactForm {
    max-width: 400px;
}

@media (min-width: 499px) {
    .Contact_heading {
        padding-left: 10%;
        font-size: 4.5em;
    }
    .Contact_info {
        padding-left: 15%;
    }
}

@media (min-width: 480px) {
    .Map_Container {
        width: 90%;
        height: 250px;
    }
    .ContactForm {
        max-width: 400px;
    }
}

@media (min-width: 768px) {
    .First_Contact__container {
        grid-template-columns: 1.8fr 3.2fr;
    }
    .Map_Container {
        width: 90%;
        height: 311px;
    }
    .ContactForm {
        max-width: 700px;
    }
}

@media (min-width: 1025px) {
    .Contact {
        width: 95%;
    }
}