.Hire {
    width: 100%;
    min-height: 220px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-flow: column;
    padding: 15px;
    box-sizing: border-box;
    color: #2C2C2C;
}

.Hire h2,
.Hire h3 {
    margin: 0;
}

.Hire h2 {
    padding: 10px 0;
    font-size: 1em;
}

.Hire h3 {
    padding: 10px 0;
    font-size: 0.77em;
}

.Hire a {
    color: inherit;
    text-decoration: none;
}

@media screen and (min-width: 499px) {
    .Hire h2 {
        font-size: 1.5em;
    }
    .Hire h3 {
        font-size: 1.17em;
    }
}