.Testimonial {
    min-height: 300px;
    padding: 70px 0;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-flow: column;
    color: #2C2C2C;
}

.Testimonial_heading {
    align-self: start;
    margin: 0;
    margin-bottom: 16px;
    text-transform: capitalize;
    text-shadow: -8px 8px 13px rgba(0, 0, 0, 0.35);
    font-size: 2.5em;
    padding: 0 15px;
}

@media (min-width: 499px) {
    .Testimonial_heading {
        padding-left: 10%;
        font-size: 4.5em;
    }
}

@media screen and (min-width: 768px) {
    .Testimonial {
        padding: 70px 15px;
    }
}

@media (min-width: 1025px) {
    .Testimonial {
        width: 95%;
    }
}