.NavigationItems {
    list-style: none;
    margin: 0;
    padding: 0;
    height: 100%;
    display: flex;
    flex-flow: column;
    align-items: left;
}

@media screen and (min-width: 768px) {
    .NavigationItems {
        flex-direction: row;
        align-items: center;
        justify-content: flex-end;
    }
}