.ToolBar {
    height: 70px;
    width: 100%;
    display: flex;
    padding: 0 10px;
    color: #242424;
    box-sizing: border-box;
    justify-content: flex-end;
    z-index: 90;
    position: fixed;
    background-image: none;
    background-size: cover;
}

.Toolbar nav {
    height: 100%;
    width: 100%;
}

.DesktopOnly {
    display: none;
}

@media screen and (min-width: 768px) {
    .ToolBar {
        background-image: url("../../../assests/images/toolbarBackground.jpg");
        background-size: cover;
    }
    .DesktopOnly {
        display: flex;
    }
}