.Btn {
    box-sizing: border-box;
    padding: 15px;
    min-width: 10rem;
    margin: 0;
    border: transparent;
    color: white;
    text-transform: uppercase;
    text-decoration: none;
    background: linear-gradient(270deg, #8743FF 0%, #4136F1 100%);
    box-shadow: 0px 15px 30px rgba(20, 102, 204, 0.16);
    border-radius: 16px;
    cursor: pointer;
}

.Btn:focus,
.Btn:hover,
.Btn:active {
    text-decoration: none;
    font-weight: bold;
    box-shadow: none;
    outline-style: none;
    color: #242424;
    border: 2px solid #4136F1;
    background: none;
}