.SideDrawer {
    position: fixed;
    width: 80%;
    max-width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: 200;
    background-size: cover;
    padding: 36px 16px;
    box-sizing: border-box;
    transition: transform 0.3 ease-out;
    background-image: url("../../../assests//images/laptopMobileBlurred.jpg");
    background-size: cover;
    background-position-x: right;
    display: flex;
    align-items: flex-start;
    flex-flow: column;
}

.Arrow {
    font-size: 2.5rem;
}

.Arrow:hover {
    cursor: pointer;
}

@media (min-width: 768px) {
    .SideDrawer {
        display: none;
    }
}

.Open {
    transform: translateY(0);
}

.Close {
    transform: translateX(-100%);
}