.Carousel_Container {
    height: 490px;
    width: 100%;
    background: #FFFFFF;
    box-shadow: -12px 12px 30px rgba(83, 83, 83, 0.25);
    border-radius: 0;
    margin-top: 50px;
    padding: 10px;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
}

.Btn {
    height: 60px;
    width: 60px;
    position: absolute;
    align-self: center;
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
    cursor: pointer;
    -webkit-transition: -webkit-transform .8s ease-in-out;
    transition: transform .8s ease-in-out;
}

