    background-image: url("data:image/svg+xml;charset=utf8,%3C?xml version='1.0' encoding='utf-8'?%3E%3C!-- Generator: Adobe Illustrator 23.0.0, SVG Export Plug-In . SVG Version: 6.00 Build 0) --%3E%3Csvg version='1.1' id='Layer_1' xmlns='http://www.w3.org/2000/svg' xmlns:xlink='http://www.w3.org/1999/xlink' x='0px' y='0px' viewBox='0 0 2000 2000' style='enable-background:new 0 0 2000 2000;' xml:space='preserve'%3E%3Cstyle type='text/css'%3E .st0{fill:url(%23SVGID_1_);} .st1{fill:none;stroke:%23FFFFFF;stroke-width:43;stroke-linecap:round;stroke-linejoin:round;stroke-miterlimit:10;} %3C/style%3E%3Cg%3E%3ClinearGradient id='SVGID_1_' gradientUnits='userSpaceOnUse' x1='650.4417' y1='394.5471' x2='1349.5583' y2='1605.4529'%3E%3Cstop offset='1.858442e-07' style='stop-color:%230013FF'/%3E%3Cstop offset='1' style='stop-color:%23C000FF'/%3E%3C/linearGradient%3E%3Ccircle class='st0' cx='1000' cy='1000' r='699'/%3E%3C/g%3E%3Cpolyline class='st1' points='954.4,1177 771.5,994 943,822.5 '/%3E%3Cpolyline class='st1' points='1229,1177 1046.1,994 1217.6,822.5 '/%3E%3C/svg%3E");
}

.Btn.right {
    right: 1px;
    background-image: url("data:image/svg+xml;charset=utf8,%3C?xml version='1.0' encoding='utf-8'?%3E%3C!-- Generator: Adobe Illustrator 23.0.0, SVG Export Plug-In . SVG Version: 6.00 Build 0) --%3E%3Csvg version='1.1' id='Layer_1' xmlns='http://www.w3.org/2000/svg' xmlns:xlink='http://www.w3.org/1999/xlink' x='0px' y='0px' viewBox='0 0 2000 2000' style='enable-background:new 0 0 2000 2000;' xml:space='preserve'%3E%3Cstyle type='text/css'%3E .st0{fill:url(%23SVGID_1_);} .st1{fill:none;stroke:%23FFFFFF;stroke-width:43;stroke-linecap:round;stroke-linejoin:round;stroke-miterlimit:10;} %3C/style%3E%3Cg%3E%3ClinearGradient id='SVGID_1_' gradientUnits='userSpaceOnUse' x1='650.4417' y1='394.5471' x2='1349.5583' y2='1605.4529'%3E%3Cstop offset='1.858442e-07' style='stop-color:%230013FF'/%3E%3Cstop offset='1' style='stop-color:%23C000FF'/%3E%3C/linearGradient%3E%3Ccircle class='st0' cx='1000' cy='1000' r='699'/%3E%3C/g%3E%3Cpolyline class='st1' points='1078.1,822.5 1261,1005.5 1089.5,1177 '/%3E%3Cpolyline class='st1' points='803.5,822.5 986.4,1005.5 814.9,1177 '/%3E%3C/svg%3E");
