.Btn.left:hover {
    -webkit-transform: rotate(-360deg);
    transform: rotate(-360deg);
}

.Btn.right:hover {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
}

@media (min-width: 1025px) {
    .Carousel_Container {
        border-radius: 32px;
        width: 80%;
        height: 450px;
    }
    .Btn {
        height: 80px;
        width: 80px;
    }
}


/* 
    ##Device = Tablets, Ipads (portrait)
    ##Screen = B/w 768px to 1024px
  */

@media (min-width: 768px) and (max-width: 1024px) {
    .Carousel_Container {
        border-radius: 32px;
        height: 460px;
        width: 95%;
    }
    .Btn {
        height: 80px;
        width: 80px;
    }
}