.Service {
    display: flex;
    justify-content: center;
    align-items: center;
}

.Service article {
    min-height: 371.82px;
    width: 290px;
    padding: 5px 10px;
    box-sizing: border-box;
    display: flex;
    flex-flow: column;
    align-items: center;
    justify-content: center;
    background-color: #FFFFFF;
    border-radius: 30px;
    box-shadow: 0 19px 38px rgba(0, 0, 0, 0.30), 0 15px 12px rgba(0, 0, 0, 0.22);
}

.Service article h4 {
    margin: 0;
    margin-bottom: 10px;
}

.Service article p {
    margin: 0;
    padding: 0;
}

.Service article img {
    width: 173px;
    height: 121px;
}

@media (min-width: 480px) {
    .Service article {
        width: 60%;
        min-height: 371.82px;
    }
}

@media (min-width: 768px) and (max-width: 1024px) {
    .Service article {
        width: 292px;
        min-height: 361.82;
    }
}

@media (min-width: 1025px) {
    .Service article {
        width: 242px;
        min-height: 356px;
    }
}